<template>
  <div id="app">
    <myresume :styleContent="currentStyleContent" ref="resume"></myresume>
    <resume-content :isMarked="isMarked" :resumeContent="currentResumeContent" ref="resumeContent"></resume-content>
  </div>
</template>

<script>
import resumeContent from './ResumeContent/ResumeContent.vue';
import myresume from './Myresume/Myresume.vue';
export default {
  name: 'app',
  data () {
    return {
      currentStyleContent: '',
      styleContent:
`
*  http://strml.net/
* 
* offer
* luck
* 
*/
/*  */
* {
  transition: all .3s;
}
/*  */
html {
  color: rgb(222,222,222);
  background: rgb(0,43,54);
}
/*  */
.resume {
  padding: .5em;
  border: 1px solid;
  overflow: auto;
  width: 90vw;
  margin: 2.5vh 5vw;
  height: 80vh;
}
/*  */
.token.selector{
  color: #CBBA7D;
}
.token.property{
  color: #98D0E9;
}
.token.punctuation{
  color: #fff;
}
.token.function{
  color: #CE773F;
}
/*  3D  */
html{
  perspective: 1000px;
}
/*  */
.resume {
  position: relative; 
  left: 0; top: 0;
  width: 40vw;
  transform: rotateY(30deg);
}
/*  */
@media screen and (max-width: 620px) {
  .resume {
    width: 90vw;
    height: 40vh;
    transform: rotateX(-30deg);
  }
}
/*  */
.editor{
  box-sizing: border-box;
  position: fixed;
  top: 0;
  right: 0;
  padding: .5em;
  margin: 2.5vh;
  width: 50vw;
  height: 85vh;
  border: 1px solid;
  background: white; 
  color: #222;
  overflow: auto;
}
/* 620px */
@media screen and (max-width: 620px) {
  .editor {
    top: 50%;
    width: 90vw;
    height: 40vh;
  }
}
/*  */

`,
    afterStyleContent:
`
/* markdown
 * html
 * marked
 */
/*  HTML  */
.editor{
  padding: 1em;
  font-size: 0.8em;
}
.editor h2{
  display: inline-block;
  font-size: 1.5em;
  margin: 0.5em;
}
.editor ul,.editor ol{
  padding: 0;
  list-style: none;
}
.editor ul> li::before{
  content: '';
  margin-right: 0.5em;
}
.editor ol {
  counter-reset: section;
}
.editor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.editor blockquote {
  padding: .5em;
  background: #ddd;
}
/*  */
`,
    currentResumeContent: '',
    // marked
    isMarked: false,
    resumeContent: 
`
## 


## 
* vue2.0
* react
* Node.js

## 
* 
* (vue+node+mysql)

## 
* 
* 
* 

## 
* 

## 
1. 
2. 
3. 

## 


----
> [GitHub](https://github.com/KnightKiz)`,
    speed: 70
    }
  },
  created () {
    this._beginning();
  },
  methods: {
    _styleEditor () {
			return new Promise((resolve, reject) => {
				let n = 0;
				let timer = window.setInterval(() => {
          this.currentStyleContent = this.styleContent.substring(0, n);
          this.$nextTick(() => {
            this.$refs.resume.goBottom();
          });
					n++;
					if (n === this.styleContent.length) {
            window.clearInterval(timer);
						resolve();
					}
				}, this.speed);
      });
    },
    _contentEditor () {
      return new Promise((resolve, reject) => {
        let n = 0;
        let timer = window.setInterval(() => {
          this.currentResumeContent = this.resumeContent.substring(0, n);
          n++;
          this.$nextTick(() => {
            this.$refs.resumeContent.goBottom();
          });
          if (n === this.resumeContent.length) {
            window.clearInterval(timer);
						resolve();
					}
        }, this.speed);
      });
    },
    _afterContentEditor () {
      return new Promise((resolve, reject) => {
        let n = 0;
        let temStr = '';
				let timer = window.setInterval(() => {
          if (n === 50) {
            this.isMarked = true;
          }
          temStr = this.afterStyleContent.substring(n, n + 1);
          this.currentStyleContent = this.currentStyleContent + temStr;
          this.$nextTick(() => {
            this.$refs.resume.goBottom();
          });
					n++;
					if (n === this.afterStyleContent.length) {
            window.clearInterval(timer);
						resolve();
					}
				}, this.speed);
      });
    },
		async _beginning () {
      await this._styleEditor();
      await this._contentEditor();
      await this._afterContentEditor();
		}
  },
  components: {
    myresume,
    resumeContent
  }
}
</script>

<style lang="stylus">
* 
  transition: all .3s
#app
  position: relative
  height: 100%
  width: 100%
</style>



